<template>
  <div id="product">
    <Box title="General information" collapsable>
      <div id="general-product-info">
        Product name <br />
        <input type="text" v-model="form.name" />

        <br /><br />

        Product description <br />
        <textarea v-model="form.description"></textarea>

        <br /><br />

        Product image <br />
        <ImageUploader id="image-uploader" v-model="form.image" />
      </div>
    </Box>

    <Box title="Product options" collapsable>
      <div>
        <div id="pricing-method-add">
          <button class="btn add" @click="addNumericProductOption">+ Numeric</button>&nbsp;
          <button class="btn add">+ Dropdown</button>&nbsp;
          <button class="btn add">+ Checkbox</button>&nbsp;
        </div>

        <div id="numeric-pricings" v-if="numericProductOptions.length > 0">
          <h2>Numeric options</h2>

          <Table>
            <template #head>
              <tr>
                <th>Option name</th>
                <th>Pricing behaviour</th>
                <th></th>
              </tr>
            </template>

            <template #body>
            <tr v-for="option in numericProductOptions">
              <td><input v-model="option.name" /></td>
              <td>
                <select>
                  <option v-for="[key, value] in Object.entries(pricingBehaviourMap)" :value="key">{{ value }}</option>
                </select>
              </td>
              <td>
                <button class="btn delete">
                  Delete
                </button>
              </td>
            </tr>
            </template>
          </Table>
        </div>
      </div>
    </Box>

    <button class="btn add text" @click="submitForm">
      <unicon :name="form.id ? 'save' : 'plus'" fill="white"></unicon> {{ form.id ? 'Edit' : 'Add' }} product
    </button>
  </div>
</template>

<script>

// Import components
import Box from "@/components/common/Box";
import Table from "@/components/common/Table";
import ImageUploader from "@/components/common/ImageUploader";

// Import services
import ProductService from "@/services/ProductService";

export default {
  components: {
    Box,
    Table,
    ImageUploader
  },

  data: () => ({
    pricingBehaviourMap: {
        'basePrice': 'Base price',
        'addative': 'Addative',
        'ignored': 'Ignored'
    },

    trigger: 0,
    form: {
      id: null,
      name: "",
      description: "",
      image: "",
      basePrice: 0,
      productOptions: []
    },
  }),

  computed: {
    numericProductOptions() {
      let a = this.trigger
      return this.form.productOptions.filter(x => x.inputType === 'numeric')
    }
  },

  async mounted() {
    // Get the product id from the URL
    const productId = this.$route.params.productId;

    // If the product id is set,
    // then retrieve the info of the product
    if (productId !== "new") {
      try {
        this.form = await ProductService.getProduct(productId);
        this.form.productOptions = []
      } catch (e) {
        notifications.add("error", "Something went wrong retrieving this product");
      }
    }
  },

  methods: {
    async submitForm() {
      await ProductService.updateProduct(this.form);

      await this.$router.push('/rms/products');
      this.$router.go(1);
      notifications.add("info", "New product created");
    },

    addNumericProductOption() {
      this.form.productOptions.push({
        inputType: 'numeric',
        pricingBehaviour: 'basePrice',
        name: 'New option'
      });

      this.trigger += 1;
    }
  }
}
</script>

<style scoped lang="scss">
@import "../../../scss/views/pages/products/product.scss";
</style>